import './CSS/General.css';
import { Switch, NavLink, Route } from "react-router-dom";
import { AnimatePresence, motion } from "framer-motion";
import { useLocation, useHistory } from 'react-router-dom/cjs/react-router-dom';
import { matchPath } from 'react-router';
import { Helmet } from "react-helmet"
import { useEffect, useState, useRef, useMemo, useReducer } from 'react';
import GetResults from "./JS/days_Calculator_Results";


const original_InnerWidth = window.innerWidth;
var page404LoadedBefore = false;

export default function App() {

  const localS = localStorage;
  var getMode = localS.getItem("Mode");
  var styleSheet = useRef();
  var alreadyLoadedOnce = useRef();
  var [transitionC, loadTransition] = useState();
  var [, forceReRender] = useReducer(x => x + 1, 0);
  var linkPath = {
    LM: `${window.location.origin}/dst/CSS/Modes/LM.css`,
    DM: `${window.location.origin}/dst/CSS/Modes/DM.css`
  }
  const transitionTime = {
    start: 350,
    end: 700
  };


  if (page404LoadedBefore) {
    page404LoadedBefore = false;
    if (getMode === "DM") document.querySelector(".imageBackground").classList.add("dark-mode");
  }

  const changeStyleSheet = mode => styleSheet.current = linkPath[mode];
  if (getMode === "LM" && alreadyLoadedOnce.current === undefined) { changeStyleSheet("LM") }
  else if (getMode === "DM" && alreadyLoadedOnce.current === undefined) changeStyleSheet("DM");

  function startTransition(mode) {
    if (mode === "LM") {

      loadTransition(TransitionComponent("LM"));

    } else if (mode === "DM") {

      loadTransition(TransitionComponent("DM"));

    }
  }
  useEffect(() => {

    if (original_InnerWidth < 630) buttonOfCalcOfDaysStyle.current = "52vw";

    switch (getMode) {
      case null:
        localS.setItem("Mode", "LM");
        changeStyleSheet("LM");
        forceReRender();
        break;

      case "DM":
        if (!document.querySelector(".imageBackground").classList.contains("page404") && alreadyLoadedOnce.current !== true) {
          document.querySelector(".imageBackground").classList.add("dark-mode");
          alreadyLoadedOnce.current = true;
        }
        break;

      default:
        break;
    }

  }, [getMode, localS]) // eslint-disable-line react-hooks/exhaustive-deps

  var isMatching;
  var location = useLocation();

  var buttonOfCalcOfDaysStyle = useRef({ width: "12vw" });
  var allLocations = ["/", "/Credits", "/section_1", "/Days_Calculator"];


  for (var i = 0; i < allLocations.length; i++) {

    var match;
    var path = allLocations[i];

    if (i === 0) {

      match = matchPath(location.pathname, { path: path, exact: true });

      if (match !== null) {
        isMatching = true;
        break;
      } else {
        isMatching = false;
        continue;
      }

    }


    match = matchPath(location.pathname, { path: path });

    if (match !== null) {
      isMatching = true;
      break;
    } else isMatching = false;

  }

  if (original_InnerWidth < 630) {
    buttonOfCalcOfDaysStyle.current = { width: "52vw" };
  }

  if (isMatching) {
    return (
      <div>
        <Helmet>
          <link rel="stylesheet" href={styleSheet.current} />
        </Helmet>

        <div>
          <AnimatePresence exitBeforeEnter>
            {transitionC}
          </AnimatePresence>
        </div>
        <div className="containerMain">
          <div id="main" className="main">
            <nav className="allPages">
              <ul>
                <li>
                  <NavLink to="/" exact>
                    Home
                  </NavLink>
                </li>
                <li>
                  <NavLink to="/Credits/">
                    Credits
                  </NavLink>
                </li>
                <li className="buttonOfCalcOfDays">
                  <NavLink to="/Days_Calculator/" style={buttonOfCalcOfDaysStyle.current}>
                    Days Calculator
                  </NavLink>
                </li>
                <button onClick={() => {

                  let getMode = localS.getItem("Mode");
                  var imageBackgroundClassList = document.querySelector(".imageBackground").classList;

                  if (getMode === "LM") {

                    startTransition("DM");

                    setTimeout(() => {

                      localS.setItem("Mode", "DM");
                      changeStyleSheet("DM");
                      if (!imageBackgroundClassList.contains("dark-mode")) imageBackgroundClassList.add("dark-mode");

                    }, transitionTime.start);

                    setTimeout(() => loadTransition(), transitionTime.end);

                  } else {

                    startTransition("LM");

                    setTimeout(() => {

                      localS.setItem("Mode", "LM");
                      changeStyleSheet("LM");
                      if (imageBackgroundClassList.contains("dark-mode")) imageBackgroundClassList.remove("dark-mode");

                    }, transitionTime.start)

                    setTimeout(() => loadTransition(), transitionTime.end);

                  }

                }}>
                  <i className="far fa-moon"></i>
                </button>
              </ul>
            </nav>
            <AnimatePresence exitBeforeEnter>
              <Switch location={location} key={location.pathname}>
                <Route exact path="/" component={IndexPage} />
                <Route path="/Credits" component={CreditsPage} />
                <Route path="/section_1" component={Section_1} />
                <Route path="/Days_Calculator" component={Days_Calculator} />
              </Switch>
            </AnimatePresence>
          </div>
        </div>
      </div>
    );
  } else {
    return <Page404 />
  }

}


const pageVariants = {
  initial: {
    opacity: 0,
    y: "-5vh"
  },
  in: {
    opacity: 1,
    y: 0
  },
  out: {
    opacity: 0
  },
  initialPage: {
    opacity: 0,
    x: "-86vw",
    y: "12vh"
  },
  inPage: {
    opacity: 1,
    x: "-31vw",
    y: "12vh"
  },
  outPage: {
    opacity: 0
  },
  inTransition: {
    x: "-0vw",
    opacity: 1
  },
  outTransition: {
    x: "100vw",
    opacity: 0
  }
};

const homeTransition = {
  type: "tween",
  ease: "anticipate",
  duration: 0.5
};

const pageTransition = {
  type: "tween",
  ease: "anticipate",
  duration: 0.8
};

function IndexPage() {

  var history = useHistory();

  return (
    <motion.div
      initial="initial"
      animate="in"
      exit="out"
      variants={pageVariants}
      transition={homeTransition}
    >
      <div className="title">
        <h2>Welcome to</h2> <h1>The Don't Starve Long guide</h1>
      </div>
      <div className="description">
        <h3>This is a fan-based site made to collect all sort of tips, tricks and guides in one organised site. </h3>
        <button className="startNowButton" onClick={() => { history.push("/section_1/") }}>
          Start Now
        </button>
      </div>
    </motion.div>
  )
}

function CreditsPage() {

  const directoryofImages = "../../dst/Credits/";
  const images = {
    SitesHub: directoryofImages + "SitesHub.jpg",
    Mr_shady: directoryofImages + "Mr.shady.jpg"
  }

  const getMode = localStorage.getItem("Mode");
  const descriptionStyle = {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "column",
    height: "71.7vh",
    color: (getMode === "DM") ? "rgb(191 191 191)" : "black"
  }

  const creditsCard_WrapperStyle = { height: "55vh" };
  const scrollPosition = useMemo(() => {

    return {
      current: 0,
      limit: {
        start: 0,
        end: 1400
      }
    };

  }, [])

  useEffect(() => {

    const removeListener = () => wrapper.removeEventListener("wheel", scrollFnc);

    const scrollFnc = (e) => {

      if (e.deltaY > 0) {

        if (scrollPosition.current === scrollPosition.limit.end) return;
        scrollPosition.current += 200;
        wrapper.scrollLeft += 200;

      } else {

        if (scrollPosition.current === scrollPosition.limit.start) return;
        scrollPosition.current -= 200;
        wrapper.scrollLeft -= 200;

      }

    }

    const wrapper = document.querySelector(".creditsCard_Wrapper");
    wrapper.addEventListener("wheel", e => scrollFnc(e));
    return removeListener;

  }, [scrollPosition])

  return (
    <motion.div
      initial="initial"
      animate="in"
      exit="out"
      variants={pageVariants}
      transition={homeTransition}
    >
      <div className="description" style={descriptionStyle}>
        <h1>Credits</h1>
        <div className="creditsCard_Wrapper" style={creditsCard_WrapperStyle}>
          <div className="creditsCard">
            <img src={images.SitesHub} alt="SitesHub" />
            <div className="links">
              <a href="https://github.com/SitesHub" target="_blank" rel="noreferrer">Github Profile</a>
              <a href="https://steamcommunity.com/id/attisalva/" target="_blank" rel="noreferrer">Steam</a>
            </div>
          </div>
          <div className="creditsCard">
            <img src={images.Mr_shady} alt="Mr.shady" />
            <div className="links">
              <a href="https://steamcommunity.com/profiles/76561198362507494/" target="_blank" rel="noreferrer">Steam</a>
            </div>
          </div>
        </div>
      </div>
    </motion.div>
  )
}

function Page404() {

  var isMobile = false;
  page404LoadedBefore = true;
  var imageBackground = document.querySelector(".imageBackground").classList;
  if (imageBackground.contains("dark-mode")) imageBackground.remove("dark-mode");
  imageBackground.add("page404");

  var history = useHistory();

  // Changes URL to /404 without reloading the page
  var wrongURL = history.location.pathname;
  var windowLocation = window.location.href.replace(wrongURL, "/");
  var basename = windowLocation.match(/(http|https):\/\/.*(\/.*\/)/)[2];
  window.history.pushState("", "", basename + "404");


  if (original_InnerWidth < 630) isMobile = true;

  return (
    <div className="containerMain">
      <div id="main" className="main">
        <motion.div
          initial="initial"
          animate="in"
          exit="out"
          variants={pageVariants}
          transition={homeTransition}
        >
          <div className="title">
            <h1>404 Error</h1>
            {
              (!isMobile)
                ? <h2>The page's broken, make sure that you have the correct URL.</h2>
                :
                <div style={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  flexDirection: "column"
                }}>
                  <h2>The page's broken,</h2>
                  <h2 style={{ fontSize: "27px" }}>make sure that you have the correct URL.</h2>
                </div>
            }
          </div>
          <button className="backButtonOf404Page" onClick={() => {
            imageBackground.remove("page404");
            history.push("/");
          }}>Go back</button>
        </motion.div>
      </div>
    </div>
  )

}


var pageName = "Home";

function Section_1() {

  const sectionTransition = {
    type: "tween",
    ease: "anticipate",
    duration: 1.2
  };

  var SectionStyle = {
    position: "relative",
    overflow: "hidden",
    left: "6.5vw",
    width: "69vw",
    height: "82vh"
  }

  if (original_InnerWidth < 630) {
    pageVariants.inPage.x = "-34vw";
    SectionStyle.width = "77vw";
  }

  return (
    <div>
      <motion.div
        initial="initial"
        animate="in"
        exit="out"
        variants={pageVariants}
        transition={sectionTransition}
      >
        <div className="titleSection">
          <h1>Section 1, Page {pageName}</h1>
        </div>
      </motion.div>
      <div style={SectionStyle}>
        <motion.div
          initial="initialPage"
          animate="inPage"
          exit="outPage"
          variants={pageVariants}
          transition={pageTransition}
          className="section"
        >
          <h2>INDEX:</h2>
          <h3>Introduction</h3>
          <h3>Navigation</h3>
          <h3>How to Play</h3>
          <h3>Basic Tips</h3>
        </motion.div>
      </div>
    </div>
  )
}

function Days_Calculator() {

  var [results, createResults] = useState(<h3 className="dataText">The data'll go here.</h3>);

  const inputsStyle = {
    position: "relative",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    top: "7vh"
  };

  const getData_Enter = (e) => {
    if (e.keyCode === 13) {
      getData()
    }
  }

  function getData() {
    var results_Wrapper = document.querySelector(".resultsTable_Wrapper");
    var days = document.getElementById("daysPassed_SearchBar").value;
    if (days === "") {
      if (results_Wrapper) {
        createResults();
        setTimeout(() => createResults(<h3 className="dataText">The data'll go here.</h3>), 500);
      }
      return;
    }
    createResults(GetResults(days));
  }


  var descriptionStyle = {};
  var daysPassed_DescriptionStyle = { marginBottom: "10px", textAlign: "center" };

  if (original_InnerWidth > 630) {
    descriptionStyle.height = "49vh";
  } else {
    descriptionStyle.height = "47vh";
    daysPassed_DescriptionStyle.fontSize = "5.7vw";
  }

  return (
    <motion.div
      initial="initial"
      animate="in"
      exit="out"
      variants={pageVariants}
      transition={homeTransition}
    >
      <div className="title">
        <h1 className="daysPassed_Title">Days Calculator</h1>
        <h2 className="daysPassed_Description" style={daysPassed_DescriptionStyle}>An useful calculator that calculates the days remaining before a certain moon cycle and season</h2>
      </div>
      <div className="description" style={descriptionStyle}>
        <div style={inputsStyle}>
          <input type="search" id="daysPassed_SearchBar" placeholder="Insert days passed" onKeyDown={(e) => { getData_Enter(e); }} />
          <button className="daysPassed_Button" onClick={() => { getData() }}>
            <i className="fas fa-calculator" />
          </button>
        </div>
        <AnimatePresence exitBeforeEnter>
          {results}
        </AnimatePresence>
      </div>
    </motion.div>
  )
}

function TransitionComponent(mode) {

  const gradients = {
    DM: "radial-gradient(circle, rgba(0 0 0) 0%, rgba(70 70 70) 100%)",
    LM: "radial-gradient(circle, rgba(222 222 222) 0%, rgba(60 60 60) 90%)"
  }
  const transition = {
    type: "tween",
    ease: "anticipate",
    duration: 0.350
  }

  return (
    <motion.div
      initial="outTransition"
      animate="inTransition"
      exit="outTransition"
      variants={pageVariants}
      transition={transition}
      style={{ zIndex: 2, position: "absolute", top: 0 }}
    >
      <div className="transitionToMode" style={
        { "--transition-color": (mode === "LM") ? gradients.LM : gradients.DM }
      }>
        <div></div>
      </div>
    </motion.div>
  )

}