import { motion } from "framer-motion";

const homeTransition = {
    type: "tween",
    ease: "anticipate",
    duration: 0.5
};

const pageVariants = {
    initial: {
        opacity: 0,
        y: "-5vh"
    },
    in: {
        opacity: 1,
        y: 0
    },
    out: {
        opacity: 0
    },
    initialPage: {
        opacity: 0,
        x: "-71vw",
        y: "12vh"
    },
    inPage: {
        opacity: 1,
        x: "-31vw",
        y: "12vh"
    },
    outPage: {
        opacity: 0
    }
};

const original_InnerWidth = window.innerWidth;
const original_InnerWidth_Mobile = original_InnerWidth < 630;

export default function GetResults(days) {

    var failed = false;
    var h4Style = {
        margin: 0,
        fontSize: "unset"
    }

    var seasonsImagePath;
    var moonCyclePath;
    var result1 = ["The days provided aren't numbers or they're less than 0", ""];
    var result2 = ["", ""];

    ((days) => {

        var limit = 20000;
        var daysN = Number(days);
        const seasons = {
            spring: {
                days: 20
            },
            summer: {
                days: 16
            },
            autumn: {
                days: 20
            },
            winter: {
                days: 15
            }
        }
        const moons = {
            new_Moon: [1, 2],
            quarter_Waxing_Crescent_Moon: [3, 4],
            half_Moon: [5, 6],
            waxing_Gibbous_Moon_3_4: [7, 8],
            full_Moon: [9, 10],
            waning_Gibbous_Moon_3_4: [11, 12],
            half_Moon2: [13, 14],
            quarter_Waning_Crescent_Moon: [15, 16]
        }
        var yearsInDays = seasons.spring.days + seasons.summer.days + seasons.autumn.days + seasons.winter.days;

        if (isNaN(daysN)) {
            if (original_InnerWidth_Mobile) h4Style.margin = "0 16px";
            if (original_InnerWidth_Mobile) h4Style.fontSize = "14px";
            return failed = true;
        }
        if (daysN < 0) {
            if (original_InnerWidth_Mobile) h4Style.margin = "0 16px";
            if (original_InnerWidth_Mobile) h4Style.fontSize = "14px";
            return failed = true;
        }
        if (daysN > limit) {
            h4Style.margin = 5;
            return result1 = ["The limit has been reached", ""];
        }

        // Gets the moon phase
        const currentPhase = {
            day: 0,
            cycles: 0
        };
        var phaseN = 0;
        var phases = [
            "new_Moon",
            "quarter_Waxing_Crescent_Moon",
            "half_Moon",
            "waxing_Gibbous_Moon_3_4",
            "full_Moon",
            "waning_Gibbous_Moon_3_4",
            "half_Moon2",
            "quarter_Waning_Crescent_Moon"
        ];

        // Transforms the excess moon cycles into normal ones
        if (daysN !== 0) {

            for (let i = 0; i < (daysN + 1); i++) {

                if (i === daysN) {

                    var day = currentPhase.day;
                    var cycles = currentPhase.cycles;

                    phaseN = day - (16 * (cycles - 1));

                } else {

                    currentPhase.day++;

                    if (i % 16 === 0) {
                        currentPhase.cycles++;
                    }

                }

            }

        }

        // Transforms the excess days into normal ones
        if (daysN > yearsInDays) {

            var countOfYears = 0;

            for (let i = 0; i < days; i++) {

                let day = i;
                if (day === yearsInDays) continue;
                if (day % yearsInDays === 0) countOfYears += 1;

            }

            daysN = daysN - (yearsInDays * countOfYears);

        }

        // Seasons calculations
        if (daysN === 0) {
            seasonsImagePath = "../Seasons/Autumn.jpg";
            result1 = ["Spring/Autumn,", `Winter in -21 day/s`];
        }

        // ----Spring/Autumn
        if (daysN < 21) {
            seasonsImagePath = "../Seasons/Autumn.jpg";
            result1 = ["Spring/Autumn,", `Winter in -${21 - daysN} day/s`];
        }

        // ----Winter
        if (daysN > 20 && daysN < 36) {
            seasonsImagePath = "../Seasons/Winter.jpg";
            result1 = ["Winter,", `Spring/Autumn in -${36 - daysN} day/s`];
        }

        // ----Spring/Autumn in the middle
        if (daysN > 35 && daysN < 55) {
            seasonsImagePath = "../Seasons/Spring.jpg";
            result1 = ["Spring/Autumn,", `Summer in -${56 - daysN} day/s`];
        }

        // ----Summer
        if (daysN > 55) {
            seasonsImagePath = "../Seasons/Summer.jpg";
            result1 = ["Summer,", `Spring/Autumn in -${72 - daysN} day/s`];
        }

        // Moon cycles
        var daysRemaining = (phaseN === 0) ? 3 : 0;
        function checkDaysRemaining(phaseN_Obj) {

            if (phaseN === phaseN_Obj[0]) {
                daysRemaining = 2;
            } else daysRemaining = 1;

        }
        var beautify = (string) => {
            var underscoresReplaced = string.replace(/_/g, " ").replace("3 4", "");
            var length = underscoresReplaced.length;
            var withoutNumbers = underscoresReplaced.substring(0, length - 1) + underscoresReplaced.charAt(length - 1).replace(" ", "");
            return withoutNumbers.charAt(0).toUpperCase() + withoutNumbers.substring(1);
        }
        if (phaseN === 0) {
            moonCyclePath = "../Moons/Moon_New.png";
            result2 = [beautify(phases[0]), `${beautify(phases[1])} in -3 days`];
            return;
        }

        // Cycles through the moon cycles
        for (let i = 0; i < phases.length; i++) {

            const phase = phases[i];
            const nextPhase = (phases[i + 1] === undefined) ? phases[0] : phases[i + 1];
            const phaseN_Obj = moons[phase];

            if (phaseN === phaseN_Obj[0] || phaseN === phaseN_Obj[1]) {

                checkDaysRemaining(phaseN_Obj);
                switch (phase) {

                    case "new_Moon":
                        moonCyclePath = "../Moons/Moon_New.png";
                        break;

                    case "quarter_Waxing_Crescent_Moon":
                        moonCyclePath = "../Moons/Moon_Quarter.png";
                        break;

                    case "half_Moon":
                        moonCyclePath = "../Moons/Moon_Half.png";
                        break;

                    case "waxing_Gibbous_Moon_3_4":
                        moonCyclePath = "../Moons/Moon_Three_Quarters.png";
                        break;

                    case "full_Moon":
                        moonCyclePath = "../Moons/Moon_Full.png";
                        break;

                    case "waning_Gibbous_Moon_3_4":
                        moonCyclePath = "../Moons/Moon_Three_Quarters.png";
                        break;

                    case "half_Moon2":
                        moonCyclePath = "../Moons/Moon_Half.png";
                        break;

                    case "quarter_Waning_Crescent_Moon":
                        moonCyclePath = "../Moons/Moon_Quarter.png";
                        break;

                    default:
                        break;
                }
                result2 = [beautify(phase), `${beautify(nextPhase)} in -${daysRemaining} day/s`];

            }

        }

    })(days)

    var resultsTableStyle = { height: "34vh" };
    var mobileImgStyle = { boxShadow: "var(--box-shadow-div)" };
    var mobileImgStyle2 = { width: "3vw", filter: "drop-shadow(var(--box-shadow-div))" };
    var descriptionStyle = {
        display: "flex",
        flexDirection: "column",
        gap: "20px",
    }
    var descriptionStyle2 = {
        display: "flex",
        flexDirection: "column",
        gap: "10px",
    }
    var h4Style2 = (original_InnerWidth_Mobile) ? {
        margin: "0 13px",
        fontSize: "16px",
        lineBreak: "anywhere"
    } : h4Style

    if (original_InnerWidth_Mobile) {
        resultsTableStyle.height = "31vh";
        mobileImgStyle.width = "51vw";
        mobileImgStyle2.width = "15vw";
        descriptionStyle.gap = "6px";
        if (!failed) h4Style.fontSize = "16px";
    }

    return (
        <motion.div
            initial="initial"
            animate="in"
            exit="out"
            variants={pageVariants}
            transition={homeTransition}
        >
            <div className="resultsTable_Wrapper">
                <div className="resultsTable" style={resultsTableStyle}>
                    <div className="seasonsDiv">
                        {(result1[1] !== "") ? <img style={mobileImgStyle} src={seasonsImagePath} alt="" /> : ""}
                        <div style={descriptionStyle}>
                            <h4 style={h4Style}>
                                {result1[0]}
                            </h4>
                            <h4 style={h4Style}>
                                {result1[1]}
                            </h4>
                        </div>
                    </div>
                    {(result2[0] !== "") ?
                        <div className="seasonsDiv">
                            <img style={mobileImgStyle2} src={moonCyclePath} alt="" />
                            <div style={descriptionStyle2}>
                                <h4 style={h4Style2}>
                                    {result2[0]}
                                </h4>
                                <h4 style={h4Style2}>
                                    {result2[1]}
                                </h4>
                            </div>
                        </div> : ""}
                </div>
            </div>
        </motion.div>
    )

}